
html,body {
  height: 100%;
  margin: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
#discordcorner {
	position: absolute;
	top: 0;
	left: 0;
	border: 0;
}

.discord-logo {
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  -webkit-transform-origin: 24px 24px;
          transform-origin: 24px 24px;
}
.discord-logo .discord-outer-layer {
  -webkit-transition: -webkit-transform 800ms cubic-bezier(0.7, 1, 0.7, 1);
  transition: -webkit-transform 800ms cubic-bezier(0.7, 1, 0.7, 1);
  transition: transform 800ms cubic-bezier(0.7, 1, 0.7, 1);
  transition: transform 800ms cubic-bezier(0.7, 1, 0.7, 1), -webkit-transform 800ms cubic-bezier(0.7, 1, 0.7, 1);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.discord-logo-container:hover .discord-outer-layer {
  -webkit-transform: scale(1.5) rotate(360deg);
          transform: scale(1.5) rotate(360deg);
}
.discord-logo .discord-middle-layer {
  -webkit-transition: -webkit-transform 800ms cubic-bezier(0.5, 1, 0.5, 1);
  transition: -webkit-transform 800ms cubic-bezier(0.5, 1, 0.5, 1);
  transition: transform 800ms cubic-bezier(0.5, 1, 0.5, 1);
  transition: transform 800ms cubic-bezier(0.5, 1, 0.5, 1), -webkit-transform 800ms cubic-bezier(0.5, 1, 0.5, 1);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.discord-logo-container:hover .discord-middle-layer {
  -webkit-transform: scale(1.4) rotate(360deg);
          transform: scale(1.4) rotate(360deg);
}
.discord-logo .discord-inner-layer {
  -webkit-transition: -webkit-transform 800ms cubic-bezier(0.3, 1, 0.3, 1);
  transition: -webkit-transform 800ms cubic-bezier(0.3, 1, 0.3, 1);
  transition: transform 800ms cubic-bezier(0.3, 1, 0.3, 1);
  transition: transform 800ms cubic-bezier(0.3, 1, 0.3, 1), -webkit-transform 800ms cubic-bezier(0.3, 1, 0.3, 1);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
.discord-logo-container:hover .discord-inner-layer {
  -webkit-transform: scale(1.3) rotate(360deg);
          transform: scale(1.3) rotate(360deg);
}
.discord-logo .discord-original {
  -webkit-transition: visibility 0ms;
  transition: visibility 0ms;
  -webkit-transition-delay: 800ms;
          transition-delay: 800ms;
}
.discord-logo-container:hover .discord-original {
  visibility: hidden;
  -webkit-transition-delay: 0ms;
          transition-delay: 0ms;
}

.github-corner {
  pointer-events: none;
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
}
.github-corner:hover .octocat{
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-animation: octocat-grow 5s ease-in-out forwards;
          animation: octocat-grow 5s ease-in-out forwards;
}
@-webkit-keyframes octocat-grow{
16%, 66%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
33%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
50%, 100%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
83%{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
}
}
@keyframes octocat-grow{
16%, 66%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
33%{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
50%, 100%{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
83%{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
}
}
.octo-click-text {
  font-weight: normal;
  text-anchor: middle;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 20px;
  stroke-width: 2px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  opacity: 0;
}
.github-corner:hover .octo-click-text {
  -webkit-animation: octocat-text 5s linear forwards;
          animation: octocat-text 5s linear forwards
}
@-webkit-keyframes octocat-text {
99% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes octocat-text {
99% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.github-corner:hover .octo-arm{
  -webkit-animation: octocat-wave 400ms linear infinite;
          animation: octocat-wave 400ms linear infinite;
}
@-webkit-keyframes octocat-wave {
0%,50%,100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
}
25% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
}
75% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
}
}
@keyframes octocat-wave {
0%,50%,100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
}
25% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
}
75% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
}
}
.github-corner .octo-glasses{
  visibility: visible;
  opacity: 0;
  -webkit-transition: opacity 5s;
  transition: opacity 5s;
}
.github-corner:hover .octo-glasses{
  opacity: 1;
  -webkit-animation: octocat-glass-wiggle 400ms linear forwards;
          animation: octocat-glass-wiggle 400ms linear forwards;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
@-webkit-keyframes octocat-glass-wiggle {
0%, 50%, 100% {
    -webkit-transform: translateX(0px) translateY(0px);
            transform: translateX(0px) translateY(0px);
}
25%, 75% {
    -webkit-transform: translateX(3px) translateY(-3px);
            transform: translateX(3px) translateY(-3px);
}
}
@keyframes octocat-glass-wiggle {
0%, 50%, 100% {
    -webkit-transform: translateX(0px) translateY(0px);
            transform: translateX(0px) translateY(0px);
}
25%, 75% {
    -webkit-transform: translateX(3px) translateY(-3px);
            transform: translateX(3px) translateY(-3px);
}
}

.rotatethis {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-transform-origin: 60px 60px;
          transform-origin: 60px 60px;
}

